.Logo
{
    position: absolute;
    width: 100%;
    height: 80PX;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 10px 15px white;
    z-index: 2;
}
.exouisideLogo{
position: absolute;
TOP: 15%;
left: 50%;
transform: translateX(-50%) scale(1.15);
z-index: 4;
}

@media(max-width: 768px){
   
    .Logo{
        display: none;
    }
  }